.triangle-bottom::before {
  --size: 1.2rem;
  position: absolute;
  display: block;
  content: '';
  width: var(--size);
  height: var(--size);
  left: 50%;
  margin-left: calc(var(--size) / -2);
  top: 100%;
  margin-top: calc(var(--size) / -2);
  bottom: 0;
  background-color: #fff;
  transform: rotate(45deg);
  box-shadow: 5px 5px 5px #dcdce4;
  border-end-end-radius: 5px;
}